import React, {useState, useEffect} from 'react';
import Modal from 'react-responsive-modal';
import { ScrollView, View, TextInput, Text } from '../../common/elements';
import Button from '../../common/button';
import RadioButton from '../../common/radio-button';
import Checkbox from '../../common/checkbox';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IUser } from '../../../models/user/user';
import { isNN } from '../../../util/index';
import { IAppUser } from '../../../models/app-user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IUserHomes } from '../../../models/store/user-homes';
import { IHome, IMember } from '../../../models/home';
import Toast from  '../../../util/toast';
import { ROLE, APPTYPE } from '../../../constants';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  userHomes: IUserHomes,
  onLoad: (id: string, sCallback: (result: IMember, homeIds: Array<string>) => void, fCallback: (message: string) => void) => void,
  onSave: (data: IMember, homeIds: Array<string>, user: IUser, users: IUsers, isApprove: Boolean, callback: (result: boolean, message: string) => void) => void,
  onAllowOwnAccess: (id: string, isAllow: boolean, callback: (result: boolean) => void) => void
};

export default (props: IProps) => {
  const [isLoading, setIsLoading] = useState(false);
  const [isShowHomes, setIsShowHomes] = useState(false);
  const [formData, setFormData] = useState({
    id: '',
    number: '',
    name: '',
    age: 0,
    gender: '',
    mobileNo: '',
    emailId: '',
    relationship: '',
    otherRelationship: '',
    type: 'TENANT',
    isApproved: false,
    isPrimary: false,
    hasOwnAccess: false,
    isOwnAccessRequested: false,
    homeIds: [] as Array<string>,
    role: {
      isAdmin: false,
      isManager: false,
      isGatekeeper: false,
      isUser: false,
      isMaintenanceSupervisor: false,
      isSecuritySupervisor: false,
      isMaintenanceManager: false,
      isSecurityManager: false
    }
  });
  
  useEffect(() => {
    let userId = props.match.params.id;
    if(userId.length > 0 && userId !== '0'){
      setIsLoading(true);
      props.onLoad(userId, (result: IMember, homeIds: Array<string>) => {
        setIsLoading(false);
        if(result && isNN(result._id)){
          setFormData({
            id: result._id,
            number: result.number,
            name: result.name,
            age: result.age,
            gender: result.gender,
            mobileNo: result.mobileNo,
            emailId: result.emailId,
            relationship: result.relationship,
            otherRelationship: result.otherRelationship,
            isApproved: result.isApproved || false,
            isPrimary: result.isPrimary,
            hasOwnAccess: result.hasOwnAccess,
            isOwnAccessRequested: result.isOwnAccessRequested,
            type: result.type,
            homeIds: homeIds,
            role: {
              isAdmin: (',' + result.role + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0,
              isManager: (',' + result.role + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0,
              isGatekeeper: (',' + result.role + ',').indexOf(',' + ROLE.GATEKEEPER + ',') >= 0,
              isUser: (',' + result.role + ',').indexOf(',' + ROLE.USER + ',') >= 0,
              isMaintenanceSupervisor: (',' + result.role + ',').indexOf(',' + ROLE.MAINTENANCE_SUPERVISOR + ',') >= 0,
              isSecuritySupervisor: (',' + result.role + ',').indexOf(',' + ROLE.SECURITY_SUPERVISOR + ',') >= 0,
              isMaintenanceManager: (',' + result.role + ',').indexOf(',' + ROLE.MAINTENANCE_MANAGER + ',') >= 0,
              isSecurityManager: (',' + result.role + ',').indexOf(',' + ROLE.SECURITY_MANAGER + ',') >= 0
            }
          });
        }else{
          Toast.fail('Failed to load User.');
        }
      }, (message: string) => {
        setIsLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load User.');
      });
    }

    /*
    if(userId > 0 && (props.users.isUpToDate && !props.users.isError && isNN(props.users.data))){
      let users = props.users.data.filter((u: IUser, i: number) => u._id === userId);
      if(isNNObject(users) && users.length > 0){
        let homeIds = [] as Array<string>;
        if(props.userHomes.isUpToDate && !props.userHomes.isError && isNN(props.userHomes.data)){
          props.userHomes.data.filter((uh: IUserHome, i: number) => uh.userId === userId).map(uh => {
            homeIds.push(uh.homeId);
          });
        }
        setFormData({...formData, 
          id: users[0]._id,
          name: users[0].name,
          mobileNo: users[0].mobileNo,
          emailId: users[0].emailId,
          ////ownername: users[0].ownername,
          ////ownermobile: users[0].ownermobile,
          homeIds: homeIds,
          role: {
            isAdmin: (',' + users[0].type + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0,
            isManager: (',' + users[0].type + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0,
            isGatekeeper: (',' + users[0].type + ',').indexOf(',' + ROLE.GATEKEEPER + ',') >= 0,
            isUser: (',' + users[0].type + ',').indexOf(',' + ROLE.USER + ',') >= 0,
          }
        });
      }
    }
    */
  }, []);
  
  const getHomes = () => {
    let homes = '';
    if(isNN(formData.homeIds) && (props.homes.isUpToDate && !props.homes.isError && isNN(props.homes.data))){
      props.homes.data.filter(d => formData.homeIds.indexOf(d._id) >= 0).map((h: IHome, i: number) => homes += (homes.length > 0 ? ', ' : '') + h.number);
    }
    return homes;
  };

  const onSave = () => {
    if((props.user.client.config.appType !== APPTYPE.QUARTERS || isNN(formData.number)) && isNN(formData.name) && formData.age > 0 && isNN(formData.gender)){
      let role = '';
      if(formData.role.isAdmin){
        role = ROLE.ADMIN
      }
      if(formData.role.isManager){
        role += (role.length > 0 ? ',' : '') + ROLE.MANAGER
      }
      if(formData.role.isGatekeeper){
        role += (role.length > 0 ? ',' : '') + ROLE.GATEKEEPER
      }
      if(formData.role.isUser){
        role += (role.length > 0 ? ',' : '') + ROLE.USER
      }
      if(formData.role.isMaintenanceSupervisor){
        role += (role.length > 0 ? ',' : '') + ROLE.MAINTENANCE_SUPERVISOR
      }
      if(formData.role.isSecuritySupervisor){
        role += (role.length > 0 ? ',' : '') + ROLE.SECURITY_SUPERVISOR
      }
      if(formData.role.isMaintenanceManager){
        role += (role.length > 0 ? ',' : '') + ROLE.MAINTENANCE_MANAGER
      }
      if(formData.role.isSecurityManager){
        role += (role.length > 0 ? ',' : '') + ROLE.SECURITY_MANAGER
      }
        setIsLoading(true);
        let member = {
          _id: formData.id,
          number: formData.number,
          name: formData.name,
          age: formData.age,
          gender: formData.gender,
          mobileNo: formData.mobileNo,
          emailId: formData.emailId ? formData.emailId.toLowerCase() : '',
          relationship: formData.relationship,
          otherRelationship: formData.otherRelationship,
          type: formData.type,
          role: role
        } as IMember;
        props.onSave(member, formData.homeIds, props.user, props.users, props.match.params.isApprove === 'true', (result: boolean, message: string) => {
          setIsLoading(false);
          if(result === true){
            setFormData({
              id: '',
              number: '',
              name: '',
              age: 0,
              gender: '',
              mobileNo: '',
              emailId: '',
              relationship: '',
              otherRelationship: '',
              isApproved: false,
              isPrimary: false,
              hasOwnAccess: false,
              isOwnAccessRequested: false,
              type: 'TENANT',
              homeIds: [],
              role: {
                isAdmin: false,
                isManager: false,
                isGatekeeper: false,
                isUser: false,
                isMaintenanceSupervisor: false,
                isSecuritySupervisor: false,
                isMaintenanceManager: false,
                isSecurityManager: false
              }
            });
            Toast.success('User saved successfully.');
          }else{
            Toast.fail(isNN(message) ? message : 'Failed to save user.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (formData.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  const onToggleHomeModel = (isShow: boolean) => {
    setIsShowHomes(isShow);
  }

  const onAllowOwnAccess = (isAllow: boolean) => {
    setIsLoading(true);
    props.onAllowOwnAccess(formData.id, isAllow, (result: boolean) => {
      setIsLoading(false);
      if(result === true){
        setFormData({
          ...formData,
          hasOwnAccess: isAllow
        });
        Toast.success('User ' + (isAllow ? 'allowed to login' : 'login access revoked') + ' successfully.');
      }else{
        Toast.fail('Failed to ' + (isAllow ? 'allow' : 'revoke') + '.');
      }
    });
  }

    return(
      <View id='mainContent'>
        <View className="row gap-20 masonry pos-r">
          <View className="masonry-item col-12">
        <Breadcrum data={[{title: 'Users', link: 'users'}, {title: 'Edit User', link: 'edit-user/0'}]} />
        <View className='bgc-white p-20'>
          {props.user.client.config.appType == APPTYPE.QUARTERS &&
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="User Id / Number" className='form-control' value={formData.number} onChange={(event: any) => setFormData({...formData, number: event.target.value})} />
          </View>
          }
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Name" className='form-control' value={formData.name} onChange={(event: any) => setFormData({...formData, name: event.target.value})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Age" className='form-control' maxLength={2} value={formData.age > 0 ? formData.age + '' : ''} onChange={(event: any) => setFormData({...formData, age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <RadioButton isActive={formData.gender === 'M'} text='Male' onPress={() => setFormData({...formData, gender: 'M'})} />
            <RadioButton isActive={formData.gender === 'F'} text='Female' onPress={() => setFormData({...formData, gender: 'F'})} />
            {props.user.client.config.hasOtherGender &&
            <RadioButton isActive={formData.gender === 'O'} text='Other' onPress={() => setFormData({...formData, gender: 'O'})} />
            }
          </View>            
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Mobile Number" className='form-control' value={formData.mobileNo} onChange={(event: any) => setFormData({...formData, mobileNo: event.target.value})} />
          </View>
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <TextInput type='text' placeholder="Email ID" className='form-control' value={formData.emailId} onChange={(event: any) => setFormData({...formData, emailId: event.target.value})} />
          </View>
          {(isNN(formData.relationship) || isNN(formData.otherRelationship)) &&
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text>{(isNN(formData.relationship) && formData.relationship != 'Other') ? formData.relationship : (isNN(formData.otherRelationship) ? formData.otherRelationship : 'Other')}</Text>
          </View>
          }
          {props.user.client.config.appType == APPTYPE.APARTMENT &&
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <RadioButton isActive={formData.type === 'OWNER'} text='Owner' onPress={() => setFormData({...formData, type: 'OWNER'})} />
            <RadioButton isActive={formData.type === 'TENANT'} text='Tenant' onPress={() => setFormData({...formData, type: 'TENANT'})} />
          </View>
          }
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mR-10'>Role: </Text>
            <Checkbox isActive={formData.role.isAdmin} text={ROLE.ADMIN} onPress={() => {
              let tRole = formData.role;
              tRole.isAdmin = !formData.role.isAdmin;
              setFormData({
                ...formData,
                role: tRole
              });
            }} />
            <Checkbox isActive={formData.role.isManager} text={ROLE.MANAGER} onPress={() => {
              let tRole = formData.role;
              tRole.isManager = !formData.role.isManager;
              setFormData({
                ...formData,
                role: tRole
              });
            }} />
            <Checkbox isActive={formData.role.isGatekeeper} text={ROLE.GATEKEEPER} onPress={() => {
              let tRole = formData.role;
              tRole.isGatekeeper = !formData.role.isGatekeeper;
              setFormData({
                ...formData,
                role: tRole
              });
            }} />
            <Checkbox isActive={formData.role.isUser} text={ROLE.USER} onPress={() => {
              let tRole = formData.role;
              tRole.isUser = !formData.role.isUser;
              setFormData({
                ...formData,
                role: tRole
              });
            }} />
            <Checkbox isActive={formData.role.isMaintenanceSupervisor} text={ROLE.MAINTENANCE_SUPERVISOR} onPress={() => {
              let tRole = formData.role;
              tRole.isMaintenanceSupervisor = !formData.role.isMaintenanceSupervisor;
              setFormData({
                ...formData,
                role: tRole
              });
            }} />
            <Checkbox isActive={formData.role.isSecuritySupervisor} text={ROLE.SECURITY_SUPERVISOR} onPress={() => {
              let tRole = formData.role;
              tRole.isSecuritySupervisor = !formData.role.isSecuritySupervisor;
              setFormData({
                ...formData,
                role: tRole
              });
            }} />
            <Checkbox isActive={formData.role.isMaintenanceManager} text={ROLE.MAINTENANCE_MANAGER} onPress={() => {
              let tRole = formData.role;
              tRole.isMaintenanceManager = !formData.role.isMaintenanceManager;
              setFormData({
                ...formData,
                role: tRole
              });
            }} />
            <Checkbox isActive={formData.role.isSecurityManager} text={ROLE.SECURITY_MANAGER} onPress={() => {
              let tRole = formData.role;
              tRole.isSecurityManager = !formData.role.isSecurityManager;
              setFormData({
                ...formData,
                role: tRole
              });
            }} />
          </View>
          {isNN(formData.id) &&
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mR-5'>Access to Login:</Text>
            <span className='d-ib bd bdc-blue-500'>
              {formData.hasOwnAccess ?
              <>
              <Text className='p-5 m-0 bgc-blue-500 c-white'>Allowed</Text>
              <Text className='p-5 m-0 c-blue-500 cur-p' onClick={() => onAllowOwnAccess(false)}>Revoke</Text>
              </>
              :
              <>
              <Text className='p-5 m-0 c-blue-500 cur-p' onClick={() => onAllowOwnAccess(true)}>Allow</Text>
              <Text className='p-5 m-0 bgc-blue-500 c-white'>Revoked</Text>
              </>
              }
            </span>
          </View>
          }
          <View className='mT-3 mB-3 pT-5 pB-5'>
            <Text className='mR-5'>House(s):</Text>
            <span className="icon-holder mR-10 cur-p" onClick={() => {
              setIsShowHomes(true);
             }}>
               <i className="c-blue-500 fa fa-pencil"></i>
            </span>
            <Text>{getHomes()}</Text>
          </View>
          {(props.homes.isUpToDate && !props.homes.isError && isNN(props.homes.data) && isShowHomes) &&
          <Modal
          open={isShowHomes}
          onClose={() => onToggleHomeModel(false)}>
            <View className='mT-30 mB-25 mL-15 mR-15' style={{height: '80%'}}>
              <ScrollView className='bd bdc-blue-500' style={{height: '50%', maxHeight: '50%'}}>
                {
                  props.homes.data.map((d: IHome, i: number) => {
                    return(
                      <Text key={'home' + i} className={'cur-p bdB p-3 mB-1' + (formData.homeIds.indexOf(d._id) >= 0 ? ' bgc-blue-500 c-white' : '')} onClick={() => {
                        if(formData.homeIds.indexOf(d._id) >= 0){
                          setFormData({...formData, 
                            homeIds: formData.homeIds.filter(e => e !== d._id)
                          });
                        }else{
                          let tHomeIds = formData.homeIds;
                          tHomeIds.push(d._id);
                          setFormData({
                            ...formData,
                            homeIds: tHomeIds
                          });
                        }
                      }}>{d.number}</Text>
                    )
                  })
                }
                </ScrollView>
              </View>
          </Modal>
          }
          <View className='mT-3 mB-3 pT-5 pB-5 ta-c'>
            <Button
            title={props.match.params.isApprove === 'true' ? 'Approve' : 'SAVE'}
            onPress={onSave}
            />
          </View>
        </View>
        <Spinner visible={isLoading} />
      </View>
      </View>
      </View>
    );
};

